import {
    Component,
    OnInit,
    Inject,
    ElementRef,
    ViewChild,
} from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MasterService } from "app/shared/services/master/master.service";
import { MatSnackBar } from "@angular/material/snack-bar";
import { VehicleService } from "app/shared/services/master/vehicle/vehicle.service";
import { Router } from "@angular/router";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { MtxDialogData } from "@ng-matero/extensions";
import { RfidService } from "app/shared/services/rfid/rfid.service";
import { DatePipe } from "@angular/common";
import { ViewEncapsulation } from "@angular/core";
import { fuseAnimations } from "@fuse/animations";
@Component({
    selector: "app-action-dialog",
    templateUrl: "./action-dialog.component.html",
    styleUrls: ["./action-dialog.component.scss"],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations,
    providers: [DatePipe],
})
export class ActionDialogComponent implements OnInit {
    params: any;
    isEditMode = false;
    form: FormGroup;
    localStorage: any;
    status = false;

    @ViewChild("toggleElement") ref: any;

    constructor(
        private fb: FormBuilder,
        private rfidService: RfidService,
        private snackBar: MatSnackBar,
        private datePipe: DatePipe,
        public dialogRef: MatDialogRef<ActionDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: MtxDialogData
    ) {}

    ngOnInit() {
        this.initialize();
    }

    initialize() {
        this.localStorage = JSON.parse(localStorage.getItem("userDetails"));

        this.params = this.data;

        this.isEditMode = this.params.action == "CREATE" ? false : true;

        this.form = this.fb.group({
            // name: ['', [Validators.required]],
            //description : ['', [Validators.required]] ,
            rfid_no: [
                "",
                [Validators.required, Validators.pattern(/^[a-zA-Z0-9]*$/)],
            ],
            rfid_expDate: ["", [Validators.required]],
        });

        if (this.isEditMode) {
            this.assignValues(this.params);
        }
    }

    close() {
        this.dialogRef.close();
    }
    closeActionPanel() {
        this.dialogRef.close();
    }

    onYesClick() {
        if (this.form.valid) {
            let formValues = this.form.value;

            const params: any = {
                name: !!formValues.name ? formValues.name : "",
                //description :formValues.description,
                rfid_no: formValues.rfid_no,
                expiry_date: this.datePipe.transform(
                    formValues.rfid_expDate,
                    "yyyy-MM-dd"
                ),
                created_date: this.datePipe.transform(
                    formValues.rfid_expDate,
                    "yyyy-MM-dd"
                ),
                created_by: this.localStorage.userId,
                status: this.status == true ? "Y" : "N",
                // tslint:disable-next-line: semicolon
            };
            // tslint:disable-next-line: no-//

            if (this.isEditMode) {
                (params.id = this.params.data.id),
                    (params.modify_by = this.localStorage.userId);
            } else {
                params.created_by = this.localStorage.userId;
            }

            if (this.isEditMode) {
                this.rfidService.update(params).subscribe(
                    (data: any) => {
                        this.snackBar.open(
                            "Record updated successfully!",
                            "Dismiss",
                            { duration: 5000 }
                        );
                        this.dialogRef.close();
                    },
                    (err) => {
                        this.snackBar.open("Please try again", "Dismiss", {
                            duration: 5000,
                        });
                    }
                );
            } else {
                this.rfidService.create(params).subscribe(
                    (data: any) => {
                        this.snackBar.open("Rfid created!", "Dismiss", {
                            duration: 5000,
                        });
                        this.dialogRef.close();
                    },
                    (err) => {
                        this.snackBar.open("Please try again", "Dismiss", {
                            duration: 5000,
                        });
                    }
                );
            }

            this.dialogRef.close(params);
        }
        return;
    }

    getErrorMessage(form: FormGroup) {
        return form.get("email").hasError("required")
            ? "You must enter a value"
            : form.get("email").hasError("email")
            ? "Not a valid email"
            : "";
    }

    assignValues(params) {
        // this.form.get('name').setValue(params.data.name);
        //this.form.get('description').setValue(params.data.description);
        this.form.get("rfid_no").setValue(params.data.rf_id_no);
        this.form.get("rfid_expDate").setValue(params.data.expiry_date);

        this.status = params.data.status == "Y" ? true : false;
    }

    updateStatus(event) {
        this.status = event.checked;
        this.ref.checked = this.status;
    }
}
